@import '../../../_variables';

.button{
  border: 1px solid rgba(255, 255, 255, .7);
  padding: 0.5rem 1rem;
  border-radius: 30px;
  background-color: transparent;
  outline: none;
  font-size: 18px;
  margin-right: 1rem;
  color: $gray;
  display: inline-flex;
  align-items: center;
  justify-content: center;

  > i {
    font-size: 24px;
    margin-right: .5rem;
  }

  &:hover{
    border: 1px solid #ffffff;

    >i{
      color: #fff;
    }
    >span{
      color: #fff;
    }
  }

}